@import-normalize; /* bring in normalize.css styles */

body {
  font-family: lato;
  line-height: 1.5;
  background-color: rgb(248, 248, 248);
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
